import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, PanelHeader, FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui';

const Home = ({ id, go }) => (
	<Panel id={id}>
		<PanelHeader>Сколько стоит проезд?</PanelHeader>
		<FormLayout>
	      <FormLayoutGroup>
	        <Input type="text" placeholder="Введите номер маршрутки/автобуса/троллейбуса?" alignment="center" />
	        <Input type="text" defaultValue="Сидоров" alignment="right" defaultValue="{Home.propTypes.currentTime}" alignment="center" />
	      </FormLayoutGroup>
	    </FormLayout>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	currentTime: "qqqqqq" 
};

export default Home;
